.container {
  width: 100%;
  min-width: 320px;
  max-width: 1000px;
  margin: 0 auto;
  padding: 0 25px;
  position: relative;
}

html, body {
  height: 100%;
}

body {
  font-size: 16px;
  min-width: 320px;
  position: relative;
  line-height: 150%;
  font-family: "PT Root";
  overflow-x: hidden;
  color: black;
  background-color: white;
}

body input, body textarea {
  border: #78BD20 1px solid;
  outline: none;
}

body input:focus:required:invalid, body textarea:focus:required:invalid {
  border: red 2px solid;
}

body input:required:valid, body textarea:required:valid {
  border: green 1px solid;
}

.header {
  width: 100%;
}

main {
  padding-bottom: 100px;
  /* */
}

footer {
  margin-top: -100px;
  /*  */
  height: 100px;
  /* */
}

.is-active {
  display: block;
  visibility: visible;
}

.hidden {
  opacity: 0;
  visibility: hidden;
}

.img-responsive {
  display: block;
  max-width: 100%;
  height: auto;
}

.img-responsive.img-width {
  width: 100%;
}

.text-center {
  text-align: center;
}

b,
strong {
  font-weight: bold;
}

li {
  list-style: none;
}

ul {
  padding: 0;
  margin: 0;
}

h1, h2, h3, h4, h5, h6 {
  margin-top: 0;
  font-weight: normal;
}

a {
  text-decoration: none;
}

a:hover {
  transition: all 0.25s ease-in-out;
}

a:link {
  color: black;
}

button, input[type="range"] {
  appearance: none;
  background-color: transparent;
  border: none;
  cursor: pointer;
}

.header {
  display: flex;
  padding-top: 15px;
  margin-bottom: 38px;
}

.header .logo__block {
  margin-right: 32px;
  align-self: center;
}

.header .logo__block .header__logo__link {
  display: inline-block;
  width: 35px;
  height: 35px;
  margin-right: 22px;
}

.header .logo__block .header__logo__link.header__logo__link--eld {
  width: 35px;
  height: 35px;
  background-color: #78BD20;
  border-radius: 10%;
}

.header .logo__block .header__logo__link.header__logo__link--eld:hover {
  transition: all 0.25s ease-in-out;
  background-color: rgba(120, 189, 32, 0.5);
}

.header .logo__block .header__logo__link.header__logo__link--eld img {
  width: 24px;
  height: 26px;
  margin-left: 4px;
  margin-top: 4px;
}

.header .logo__block .header__logo__link.header__logo__link--mvideo {
  width: 35px;
  height: 35px;
  background-color: #ED1D24;
  border-radius: 100%;
  margin-right: 0;
}

.header .logo__block .header__logo__link.header__logo__link--mvideo:hover {
  transition: all 0.25s ease-in-out;
  background-color: rgba(237, 29, 36, 0.5);
}

.header .logo__block .header__logo__link.header__logo__link--mvideo img {
  width: 23px;
  height: 17px;
  margin-left: 6px;
  margin-top: 4px;
}

.header__title {
  font-size: 14px;
  color: rgba(0, 0, 0, 0.7);
  align-self: center;
  font-weight: 500;
  padding: 0;
  margin: 0;
}

.burger__label {
  cursor: pointer;
  display: block;
  width: 24px;
  height: 16px;
  box-sizing: border-box;
}

.burger__block {
  width: 32px;
  height: 32px;
  margin-top: 14px;
  align-self: center;
  margin-left: auto;
  display: block;
  cursor: pointer;
}

.burger__block .burger {
  display: block;
  width: 24px;
  height: 2px;
  background-color: #ED1D24;
}

.burger__block .burger:before {
  content: '';
  position: relative;
  top: -8px;
  display: block;
  width: 24px;
  height: 2px;
  background-color: #ED1D24;
  transition: transform .3s linear;
}

.burger__block .burger:after {
  content: '';
  display: block;
  position: relative;
  bottom: -6px;
  width: 24px;
  height: 2px;
  background-color: #ED1D24;
  transition: transform .3s linear;
}

