#menuToggle:checked + .burger__label .burger {
  visibility: hidden;
}

#menuToggle:checked + .burger__label .burger:before {
  transform: rotate(40deg);
  top: 0;
  visibility: visible;
  transition: transform .3s linear;
}

#menuToggle:checked + .burger__label .burger:after {
  transform: rotate(-40deg);
  bottom: 2px;
  visibility: visible;
  transition: transform .3s linear;
}

